import React from 'react';
import { Link } from 'gatsby'

import {Row, Col} from 'react-bootstrap';

const styles = {
  cardWork: {
    marginTop: '75px',
    borderRadius: '3px',

    image: {
      backgroundColor: 'white',
    },

    date: {
      opacity: '0.8',
    },

    description: {
      marginTop: '50px',
    }
  }
}
 
const CardWork = ({slug, frontmatter}) => (
  <Col style={styles.cardWork} md={5} mdOffset={1}>
    <Row>
      <Col md={12}>
        <div style={styles.cardWork.image}>

        </div>
      </Col>
      <Col md={12}>
        <Link to={slug} className="post-preview">
            <div className="post-list">
              <h1>{frontmatter.title}</h1>
              <p style={styles.cardWork.date}>{frontmatter.date}</p>
              <p style={styles.cardWork.description} className='emphasize'>{frontmatter.description}</p>
            </div>
        </Link>
      </Col>
    </Row>
  </Col>
)
export default CardWork;