import React, {Component} from 'react';

//development: 0.5, interactionDesign: 0.2, conceptGeneration: 0.3, userTesting: 0 }}, 

class ExperienceGraph extends Component {
  render() {
    const {width, height, data} = this.props    
    const xInc = width/(data[0].data.length+1);    

    const smoothing = 0.15

    let accData = data[0].data.map( el => 0);

    data.forEach( (work) => {
      work.data.forEach( (el, i) => {
        accData[i] += el;
      })
    });

    const max = Math.max(...accData);
   
    
    const options = {
      yMin: 0,
      yMax: max,
      xMin: 0,
      xMax: width
    }
    
    const map = (value, inMin, inMax, outMin, outMax) => {
      return (value - inMin) * (outMax - outMin) / (inMax - inMin) + outMin;
    };
        
    const pointsPositionsCalc = (points, w, h, options) => points.map(e => {
      const x = map(e[0], options.xMin, options.xMax, 0, w)
      const y = map(e[1], options.yMin, options.yMax, 0, h)
      return [x, y]
    });
    
    const line = (pointA, pointB) => {
      const lengthX = pointB[0] - pointA[0]
      const lengthY = pointB[1] - pointA[1]
      return {
        length: Math.sqrt(Math.pow(lengthX, 2) + Math.pow(lengthY, 2)),
        angle: Math.atan2(lengthY, lengthX)
      }
    }
    
    const controlPoint = (line, smooth) => (current, previous, next, reverse) => {
      const p = previous || current
      const n = next || current
      const l = line(p, n)
    
      const angle = l.angle + (reverse ? Math.PI : 0)
      const length = l.length * smooth
      const x = current[0] + Math.cos(angle) * length
      const y = current[1] + Math.sin(angle) * length
      return [x, y]
    }
    
    const bezierCommand = (controlPoint) => (point, i, a) => {
      const cps = controlPoint(a[i - 1], a[i - 2], point)
      const cpe = controlPoint(point, a[i - 1], a[i + 1], true)
      const close = i === a.length - 1 ? '':''
      return `C ${cps[0]},${cps[1]} ${cpe[0]},${cpe[1]} ${point[0]},${point[1]}${close}`
    }
    
    const svgPath = (points, command, h) => {
      const d = points.reduce((acc, e, i, a) => i === 0
          ? `M ${a[a.length - 1][0]},0 L ${e[0]},0 L ${e[0]},${e[1]}`
          : `${acc} ${command(e, i, a)}`
      , '')
    
      return d;
    }
    
    const w = width;
    const h = height;

    const bezierCommandCalc = bezierCommand(controlPoint(line, smoothing))
    return (
      <svg width={width} height={height}>
        {data.map( (work, i) => {

          let thisPoints = [
            [0,0],
            [xInc*1, accData[0]],
            [xInc*2, accData[1]],
            [xInc*3, accData[2]],
            [xInc*4, accData[3]],
            [xInc*5, 0] ];

          const points = pointsPositionsCalc(thisPoints, w, h, options);

          work.data.forEach( (el, i) => {
            accData[i] -= el;
          })

          return <g key={i} className="graph-path">
            <path d={svgPath(points, bezierCommandCalc, h)} fill={`rgb(${255/data.length*i})`} />       
          </g>
          })}
      </svg>
    )
  }
}


export default ExperienceGraph;

